import TextField from '@material-ui/core/TextField';
import { css } from 'emotion';
import React from 'react';

const DisplayFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    /* background-color: #fafafa; */
  }
  .MuiInput-input {
    padding: 2px 0;
    font-size: 14px;
  }
`;
interface Props {
  label: string;
  className?: string;
  // type?: 'text' | 'number' | 'password';
  disabled?: boolean;
  value: string;
  transform?: (value: string) => any;
}

const DisplayField = (props: Props) => {
  const {
    label,
    disabled,
    value,
  } = props;

  return (
    <TextField
      fullWidth
      className={DisplayFieldStyle}
      // type={ type }
      label={label}
      value={value}
      InputLabelProps={{ shrink: true }}
      disabled={disabled}
    />
  );
};

export default DisplayField;
