import MuiTextField from '@material-ui/core/TextField';
import NumberFormat from 'react-number-format';
import React from 'react';
import { FieldName } from 'react-hook-form/dist/types/form';
import {
  Control,
  Controller,
  FieldError,
  // FieldName
} from 'react-hook-form';
import { FieldRules } from 'types/FieldRules';
import { css } from 'emotion';

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

interface Props<T extends Record<string, any>> {
  name: keyof T;
  control: Control<T>;
  label: string;
  error?: FieldError;
  rules?: FieldRules;
  // TODO: Implement min/max
  max?: number;
  min?: number;
  // type?: 'text' | 'number' | 'password';
  disabled?: boolean;
  transform?: (value: string) => any;
}

interface NumberFormatCustomProps {
  inputRef: (instance: NumberFormat | null) => void;
  onChange: (event: { target: { name: string; value: string } }) => void;
  name: string;
}

const NumberFormatCustom = (props: NumberFormatCustomProps) => {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      max={100}
      min={0}
      getInputRef={inputRef}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value
          }
        });
      }}
      decimalScale={2}
      fixedDecimalScale
      thousandSeparator
      isNumericString
      suffix="%"
    />
  );
}

const PercentField = <T extends object>(props: Props<T>) => {
  const {
    name,
    control,
    label,
    error,
    rules,
    // type,
    disabled,
    transform
  } = props;

  return (
    <Controller
      control={control}
      name={name as FieldName<T>}
      rules={rules}
      render={({ onChange, onBlur, value, name }) => (
        <MuiTextField
          id={name}
          fullWidth
          // type={ type }
          className={TextFieldStyle}
          label={label}
          onChange={(event) => {
            if (transform) {
              onChange(transform(event.target.value));
            } else {
              onChange(event.target.value);
            }
          }}
          onBlur={onBlur}
          value={value}
          error={!!error}
          InputLabelProps={{ shrink: true }}
          helperText={error?.message ?? ''}
          disabled={disabled}
          InputProps={{
            inputComponent: NumberFormatCustom as any
          }}
        />
      )}
    />
  );
};

export default PercentField;
